import React from "react";
import {Cell, Legend, Pie, PieChart, PieLabelRenderProps, Sector} from "recharts";
import {Box} from "@material-ui/core";

interface WykresKolowyProps {
    dane: any[];
    opis?: string;
    procentCalosci: number;
}

export class WykresKolowy extends React.Component<WykresKolowyProps> {
    constructor(props: WykresKolowyProps) {
        super(props);

        this.legendRenderer = this.legendRenderer.bind(this);
    }

    RADIAN = Math.PI / 180;
    state = {
        activeIndex: -1,
        verticalOffset: 0,
    };

    onPieClick = (data: PieLabelRenderProps, index: number) => {
        const {midAngle} = data;
        const sin = Math.sin(-this.RADIAN * (midAngle || 0));
        this.setState({
            verticalOffset: (sin < 0 && this.state.activeIndex !== index ? 40 : 0),
            activeIndex: (this.state.activeIndex === index? -1 : index),
        });
    };

    // @ts-ignore
    legendRenderer(value, entry, index) {
        if (index === this.state.activeIndex) {
            return <span><b>{value}</b></span>;
        } else {
            return <span>{value}</span>;
        }
    }

    render() {
        // @ts-ignore
        const renderActiveShape = (props) => {
            const {
                cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle,
                percent, value,
            } = props;
            const fill = '#f9c00c';
            const sin = Math.sin(-this.RADIAN * midAngle);
            const cos = Math.cos(-this.RADIAN * midAngle);
            const sx = cx + (outerRadius + 10) * cos;
            const sy = cy + (outerRadius + 10) * sin;
            const mx = cx + (outerRadius + 30) * cos;
            const my = cy + (outerRadius + 30) * sin;
            const ex = mx;
            const ey = cy + ((outerRadius/0.86 + 18) * (sin >= 0 ? 1 : -1)) - 5;
            const textAnchor = cos < 0 ? 'start' : 'end';
            const procentCalosci = this.props.procentCalosci;
            return (
                <g>
                    <Sector
                        cx={cx + 7 * cos}
                        cy={cy + 7 * sin}
                        innerRadius={innerRadius}
                        outerRadius={outerRadius + 3}
                        startAngle={startAngle}
                        endAngle={endAngle}
                        fill={fill}
                        stroke="black"
                    />
                    <path d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`} stroke={fill} fill="none" />
                    <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none" />
                    <text x={ex + (cos < 0 ? 1 : -1) * 12} y={ey} textAnchor={textAnchor} fill="#333">{`${Math.round(value/1000)} tys. zł`}</text>
                    <text x={ex + (cos < 0 ? 1 : -1) * 12} y={ey} dy={18} textAnchor={textAnchor} fill="#999">
                        {`(${(percent * 100).toFixed(2)}%${procentCalosci === 100 ?'':' / ' + (1.0 * procentCalosci * percent).toFixed(2) + '%'})`}
                    </text>
                </g>
            );
        };

        const {dane, opis = 'bla'} = this.props;
        let daneFinal: any[] = dane.slice(0, 8);
        if (dane.length > 8) {
            daneFinal.push({name: "Pozostałe", value: dane.slice(8).reduce((a, b) => a + b.value, 0)});
        }
        // https://learnui.design/tools/data-color-picker.html#palette
        const COLORS = ["grey", "#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600"].reverse();
        // const HUE = [
        //     ["#003f5c", "#255571", "#406b88", "#59829e", "#739bb6", "#8db3ce", "#a7cde6", "#c2e7ff"],
        //     ["#2f4b7c", "#475f8e", "#5f73a0", "#7688b2", "#8d9ec5", "#a5b4d8", "#bdcbeb", "#d6e2ff"],
        // ];
        return <React.Fragment>
            {opis?<React.Fragment><br/><Box className="intro" dangerouslySetInnerHTML={{__html: opis}} /></React.Fragment>:<React.Fragment/>}
            <PieChart width={600} height={200 + (this.state.activeIndex >= 0 ? 40:0)}>
            <Pie data={daneFinal} dataKey="value" nameKey="name" cx="50%" cy={100+this.state.verticalOffset}
                 innerRadius={20} outerRadius={86} label={false} startAngle={450} endAngle={90} activeIndex={this.state.activeIndex}
                 onClick={this.onPieClick}
                 activeShape={renderActiveShape}>
                {
                    // @ts-ignore
                    daneFinal.map((entry, index) => (
                        <Cell key={`cell-${index}`} fill={index === this.state.activeIndex?"#f9c00c":COLORS[index]}/>
                    ))
                }
            </Pie>
            <Legend layout="vertical" verticalAlign="top" align="right" formatter={this.legendRenderer}/>
            </PieChart>
        </React.Fragment>;
    }
}