import {PowiatEls, SelektorGminy} from "./SelektorGminy";
import {Dane, Panel} from "./Panel";
import axios from "axios";
import { cacheAdapterEnhancer } from 'axios-extensions';
import {SelektorWojewodztwa} from "./SelektorWojewodztwa";
import {WyborModulow} from "./WyborModulow";
import { ZgonyProps } from './Zgony';
import {Zatrudnienie} from "./Nauczyciele";
import {Uczniowie} from "./Uczniowie";
import {GUS, GUSProps} from "./GUSComp";
import {SelektorGrupy} from "./SelektorGrupy";
