const http = axios.create({
    baseURL: '/',
    // headers: { 'Cache-Control': 'no-cache' },
    // cache will be enabled by default
    adapter: axios.defaults.adapter?cacheAdapterEnhancer(axios.defaults.adapter):undefined
});

interface AppProps {
}

interface AppState {
    cytat: string;
    autor: string;
    select: string[];
    dzial: string;
    grupaDzial: string;
    grupaRozdzial: string;
    gus_opis: string;
    gus_id: string;
    gus_comps: GUSProps[];
    dane?: Dane;
    powiaty?: Map<string, PowiatEls>;
    zgony: ZgonyProps;
    nauczyciele: Zatrudnienie[];
    uczniowie: Uczniowie[];
}

class App extends React.Component<AppProps, AppState> {
    classes : any;
    componentRef : any;

    WOJEWODZTWA = [
        "DOLNOŚLĄSKIE",
        "KUJAWSKO-POMORSKIE",
        "LUBELSKIE",
        "LUBUSKIE",
        "ŁÓDZKIE",
        "MAŁOPOLSKIE",
        "MAZOWIECKIE",
        "OPOLSKIE",
        "PODKARPACKIE",
        "PODLASKIE",
        "POMORSKIE",
        "ŚLĄSKIE",
        "ŚWIĘTOKRZYSKIE",
        "WARMIŃSKO-MAZURSKIE",
        "WIELKOPOLSKIE",
        "ZACHODNIOPOMORSKIE",
    ]

    MODULY = [
        "budzet",
        "smieci",
        "dotacje_ue",
        "dzialy",
        "rozdzialy",
        "grupy-dzialy",
        "grupy-rozdzialy",
        "nauczyciele",
        "uczniowie",
        "zgony",
        "gus",
    ]

    GRUPY = [
        "Wynagrodzenia i składki",
        "Wydatki na zadania statutowe",
        "Dotacje na zadania bieżące",
        "Świadczenia na rzecz osób fizycznych",
        "Wynagrodzenia finansowane programami UE",
        "Współfinansowanie wynagrodzeń programów UE",
        "Wynagrodzenia dla innych programów zagranicznych",
        "Wydatki finansowane programami UE",
        "Współfinansowanie wydatków programów UE",
        "Wydatki dla innych programów zagranicznych",
        "Dotacje bieżące z programów UE",
        "Współfinansowanie dotacji bieżących z programów UE",
        "Dotacje bieżące z innych programów zagranicznych",
        "Świadczenia dla osób fizycznych finansowane z UE",
        "Współfinansowane świadczeń dla osób fizycznych z UE",
        "Świadczenia dla osób fizycznych z innych programów zagranicznych",
        "Wypłaty z poręczeń i gwarancji udzielonych przez gminę",
        "Obsługa długu",
        "Inwestycje ze środków gminy",
        "Inwestycje ze środków UE",
        "Współfinansowanie inwestycji ze środków UE",
        "Dotacje do inwestycji ze środków gminy",
        "Dotacje do inwestycji ze środków UE",
        "Współfinansowanie dotacji do inwestycji ze środków UE",
        "Zakup i objęcie akcji i udziałów",
        "Wniesienie wkładów do spółek prawa handlowego",
    ]

    constructor(props: AppProps) {
        super(props);
        this.componentRef = React.createRef();

        this.handleChangeAutor = this.handleChangeAutor.bind(this);
        this.handleChangeCytat = this.handleChangeCytat.bind(this);
        this.handleChangeMultiple = this.handleChangeMultiple.bind(this);

        this.pobierzDaneGminy = this.pobierzDaneGminy.bind(this);
        this.pobierzListePowiatow = this.pobierzListePowiatow.bind(this);
        this.handleChangeGrupyDzialy = this.handleChangeGrupyDzialy.bind(this);
        this.handleChangeGrupyRozdzialy = this.handleChangeGrupyRozdzialy.bind(this);

        this.handleSubmit = this.handleSubmit.bind(this);

        this.state = {
            cytat: "np: Kraków w 2019 realizował dobrą politykę budżetową. Wprawdzie wydatki przewyższyły dochody, ale środki te zostały wydane na wkład własny w projekty inwestycyjne dofinansowane przez UE, które będą procentować w przyszłych latach.",
            autor: "np.: Maciej Pikor, Lider Polska 2050 w Małopolsce",
            select: this.MODULY,
            dzial: "0",
            grupaDzial: "Wynagrodzenia i składki",
            grupaRozdzial: "Wynagrodzenia i składki",
            gus_opis: "Procent mieszkańców podłączonych do kanalizacji|miasto,ogółem,wieś",
            gus_id: "60308,60309,60310",
            gus_comps: [],
            zgony: {podregion: '', zgony: []},
            nauczyciele: [],
            uczniowie: [],
        };
    }

    handleChangeAutor(autor: string) {
        this.setState(Object.assign({}, this.state, {autor: autor}));
    }

    handleChangeCytat(cytat: string) {
        this.setState(Object.assign({}, this.state, {cytat: cytat}));
    }

    handleChangeMultiple(values: string[]) {
        this.setState(Object.assign({}, this.state, {select: values}));
    }

    handleChangeLiczbaDzialow(liczbaDzialow: string) {
        this.setState(Object.assign({}, this.state, {liczbaDzialow: liczbaDzialow}));
    }

    handleChangeLiczbaRozdzialow(liczbaRozdzialow: string) {
        this.setState(Object.assign({}, this.state, {liczbaRozdzialow: liczbaRozdzialow}));
    }

    handleChangeDzial(dzial: string) {
        this.setState(Object.assign({}, this.state, {dzial: dzial}));
    }

    handleChangeGrupyDzialy(grupa: string) {
        this.setState(Object.assign({}, this.state, {grupaDzial: grupa}));
    }

    handleChangeGrupyRozdzialy(grupa: string) {
        this.setState(Object.assign({}, this.state, {grupaRozdzial: grupa}));
    }

    handleSubmit(event: FormEvent) {
        event.preventDefault();
    }

    isShown(name: string) {
        return (this.state.select.indexOf(name) > -1)
    }

    //TODO: use redux
    render() {
        const {cytat, autor, select, dzial, grupaDzial, grupaRozdzial, gus_opis,
            gus_id, dane, powiaty, zgony, nauczyciele, uczniowie, gus_comps} = this.state;
        return (
            <Box display="flex" flexDirection="row">
                <Panel dane={dane} ref={this.componentRef} cytat={cytat} autor={autor} selected={select} dzial={dzial}
                       grupaDzial={grupaDzial} grupaRozdzial={grupaRozdzial} zgony={zgony} nauczyciele={nauczyciele}
                       uczniowie={uczniowie} gus_comps={gus_comps}/>
                <form onSubmit={this.handleSubmit}>
                    <Box display="flex" flexDirection="column" style={{padding: 10}}>
                        <strong>Wybór Jednostki Samorządu:</strong>
                        <SelektorWojewodztwa wojewodztwa={this.WOJEWODZTWA} wojewodztwoChangeFn={this.pobierzListePowiatow}/>
                        <SelektorGminy powiaty={powiaty} gminaChangeFn={this.pobierzDaneGminy}/>
                        <hr/>
                        <strong>Komentarz eksperta:</strong>
                        <textarea name="cytat" onChange={ev=>this.handleChangeCytat(ev.target.value)} value={cytat}
                                  style={{marginBottom: 10}}/>
                        <input type="text" name="autor" onChange={ev=>this.handleChangeAutor(ev.target.value)}
                               value={autor}/>
                        <hr/>
                        <strong>Elementy grafiki:</strong>
                        <WyborModulow moduly={this.MODULY} zmianaModuluFn={this.handleChangeMultiple}/>
                        <hr/>
                        {this.isShown('rozdzialy')?<React.Fragment>
                                <strong>Dział do szczegółowego wyświetlenia</strong>
                                <input type="text" name="dzial" value={dzial} style={{marginBottom: 10}}
                                       onChange={ev=>this.handleChangeDzial(ev.target.value)}/>
                                <hr/></React.Fragment>:<React.Fragment/>}
                        {this.isShown('grupy-dzialy')?<React.Fragment>
                                <strong>Podział wydatków z grupy na działy:</strong>
                                <SelektorGrupy grupy={this.GRUPY} grupaChangeFn={this.handleChangeGrupyDzialy}/>
                                <hr/></React.Fragment>:<React.Fragment/>}
                        {this.isShown('grupy-rozdzialy')?<React.Fragment>
                                <strong>Podział wydatków z grupy na rozdziały:</strong>
                                <SelektorGrupy grupy={this.GRUPY} grupaChangeFn={this.handleChangeGrupyRozdzialy}/>
                                <hr/></React.Fragment>:<React.Fragment/>}
                        {this.isShown('gus')?<React.Fragment><strong>Dodatkowy wykres z danych GUS:
                            (<a target="_blank" href="https://bdl.stat.gov.pl/BDL/metadane/cechy/2398#">źródło</a>)</strong>
                                <DebounceInput type="text" name="gus_opis" value={gus_opis} style={{marginBottom: 10}}
                                       onChange={ev=>this.pobierzGUSComps(dane?.kod_gus || '', gus_id, ev.target.value.trim())}
                                               debounceTimeout={2000}/>
                                <DebounceInput type="text" name="gus_id" value={gus_id} style={{marginBottom: 10}}
                                       onChange={ev=>this.pobierzGUSComps(dane?.kod_gus || '', ev.target.value.trim(), gus_opis)}
                                               debounceTimeout={2000}/>
                                <hr/></React.Fragment>:<React.Fragment/>}

                        <button onClick={() => exportComponentAsPNG(this.componentRef)}>
                            Export PNG
                        </button>
                    </Box>
                </form>
            </Box>
        );
    }

    componentDidMount() {
        this.pobierzListePowiatow("DOLNOŚLĄSKIE");
    }

    pobierzListePowiatow(wojewodztwo: string) {
        let wojName = wojewodztwo.replaceAll("Ś", "S").replaceAll("Ą", "A").replaceAll("Ń", "N").replaceAll("Ł", "L");
        const apiUrl = '/files/' + wojName + '.json';
        http.get(apiUrl).then((response) => {
            const ret = new Map<string, PowiatEls>();
            for (const x in response.data) {
                if (response.data.hasOwnProperty(x)) {
                    const gminy = new Map<string, string>();
                    for (const y in response.data[x]) {
                        if (response.data[x].hasOwnProperty(y)) {
                            gminy.set(y, response.data[x][y]);
                        }
                    }
                    const p: PowiatEls = {gminy: gminy};
                    ret.set(x, p);
                }
            }
            this.setState(Object.assign({}, this.state, {powiaty: ret}));
            const pierwszaGmina = ret.values().next().value.gminy.values().next().value;
            this.pobierzDaneGminy(pierwszaGmina);
        });
    }

    pobierzDaneGminy(kodGminy: string) {
        const apiUrl = '/files/' + kodGminy + '-panel.json';
        http.get(apiUrl).then((response) => {
            const ret: Dane = response.data;
            console.log(ret);
            this.setState(Object.assign({}, this.state, {dane: ret}));
            if (ret.podregion_eurostat) {
                this.pobierzZgony(ret.podregion_eurostat)
            }
            if (ret.kod_gus) {
                this.pobierzNauczycieli(ret.kod_gus);
                this.pobierzUczniowie(ret.kod_gus);
                this.pobierzGUSComps(ret.kod_gus, this.state.gus_id, this.state.gus_opis)
            }
        });
    }

    pobierzNauczycieli(kodGus: string) {
        let szkoly = {
            288152: "przedszkola",
            288154: "przedszkola",  // oddziały przy SP
            288155: "przedszkola",  // zespoły
            288156: "przedszkola",  // punkty
            377896: "podstawowe",   // bez specjalnych
            377897: "podstawowe",   // specjalne
            377895: "podstawowe",   // dla dorosłych
            378306: "zawodowe",
            378248: "gimnazja",
            378626: "licea",
            378627: "licea",        // specjalne
            378628: "licea",        // dla dorosłych
            378629: "licea",        // uzupełniające
            378630: "licea",        // uzupełniające specjalne
            378631: "licea",        // uzupełniające dla dorosłych
        }
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        for (let id in szkoly) {
            apiUrl += "&var-id=" + id;
        }
        let ret: Zatrudnienie[] = [{rok: 2015}, {rok: 2016}, {rok: 2017}, {rok: 2018}, {rok: 2019}];
        http.get(apiUrl).then((response) => {
            for (let r of response.data.results) {
                for (let v of r.values) {
                    for (let z of ret) {
                        if (z.rok === Number.parseInt(v.year)) {
                            // @ts-ignore
                            if (z[szkoly[r.id]] !== undefined) {
                                // @ts-ignore
                                z[szkoly[r.id]] += v.val;
                            } else {
                                // @ts-ignore
                                z[szkoly[r.id]] = v.val;
                            }
                        }
                    }
                }
            }
            this.setState(Object.assign({}, this.state, {nauczyciele: ret}));
        });
    }

    pobierzUczniowie(kodGus: string) {
        let szkoly = {
            377227: "zlobki",
            151864: "przedszkola",
            151867: "podstawowe",   // bez specjalnych
            46733:  "podstawowe",   // oddziały specjalne przy SP
            46726:  "podstawowe",   // SP dla niepełnosprawnych
            455415: "zawodowe",
            2394:   "gimnazja",
            455392: "licea",        // razem
        }
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        for (let id in szkoly) {
            apiUrl += "&var-id=" + id;
        }
        let ret: Uczniowie[] = [{rok: 2015}, {rok: 2016}, {rok: 2017}, {rok: 2018}, {rok: 2019}];
        http.get(apiUrl).then((response) => {
            for (let r of response.data.results) {
                for (let v of r.values) {
                    for (let z of ret) {
                        if (z.rok === Number.parseInt(v.year)) {
                            // @ts-ignore
                            if (z[szkoly[r.id]] !== undefined) {
                                // @ts-ignore
                                z[szkoly[r.id]] += v.val;
                            } else {
                                // @ts-ignore
                                z[szkoly[r.id]] = v.val;
                            }
                        }
                    }
                }
            }
            this.setState(Object.assign({}, this.state, {uczniowie: ret}));
        });
    }

    pobierzGUSComps(kodGus: string, ids_string: string, opis: string) {
        this.setState(Object.assign({}, this.state, {gus_opis: opis, gus_id: ids_string}));
        let ids = ids_string.split(",").map(x => parseInt(x));
        let apiUrl = 'https://bdl.stat.gov.pl/api/v1/data/by-unit/' + kodGus + '?lang=pl&page-size=100';
        ids.forEach(id => {
            apiUrl += "&var-id=" + id;
        });
        let ret: GUSProps[] = [];
        http.get(apiUrl).then((response) => {
            let dane: GUS[] = [{rok: 2015}, {rok: 2016}, {rok: 2017}, {rok: 2018}, {rok: 2019}];
            for (let r of response.data.results) {
                for (let i = 0; i < ids.length; i++) {
                    // znajdzmy odpowiedni id do przypisania wartosci, aby byly w takiej kolejnosci jak na liscie
                    if (ids[i] !== r.id) {
                        continue;
                    }
                    for (let v of r.values) {
                        for (let z of dane) {
                            if (z.rok === Number.parseInt(v.year)) {
                                // @ts-ignore
                                z["dane" + i] = v.val;
                            }
                        }
                    }
                }
            }
            let props = {opis: opis, dane: dane}
            ret.push(props);
            this.setState(Object.assign({}, this.state, {gus_comps: ret}));
        });
    }

    pobierzZgony(podregion: string) {
        const apiUrl = '/files/' + podregion + '.json';
        http.get(apiUrl).then((response) => {
            let x = response.data
            this.setState(Object.assign({}, this.state, {zgony: x}));
        });
    }
}

export default App;
