import React from "react";
import {Box} from "@material-ui/core";
import {CartesianGrid, Legend, Line, LineChart, XAxis, YAxis} from "recharts";

export interface GUS {
    rok: number;
}

export interface GUSProps {
    opis: string;
    dane: GUS[];
}

export class GUSComp extends React.Component<GUSProps> {
    render() {
        const {dane, opis} = this.props;
        let [tekst, serie_string] = opis.split('|');
        let serie = serie_string?serie_string.split(","):[];
        const COLORS = [
            [],
            ["#003f5c"],
            ["#003f5c", "#ffa600"],
            ["#003f5c", "#bc5090", "#ffa600"],
            ["#003f5c", "#7a5195", "#ef5675", "#ffa600"],
            ["#003f5c", "#58508d", "#bc5090", "#ff6361", "#ffa600"],
            ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a"],
            ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43"],
            ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600"],
        ];
        return <Box><br/><Box>{tekst}</Box>
            <LineChart syncId={opis} width={530} height={250} data={dane}
                   margin={{ top: 5, right: 15, left: 10, bottom: 5 }}>
            <CartesianGrid strokeDasharray="5 5" />
            <XAxis dataKey="rok" />
            <YAxis yAxisId="left" orientation="left"/>
            <YAxis yAxisId="right" orientation="right"/>
            <Legend />
                {serie.map((value, index) => {
                    console.log(value.indexOf('R:'));
                    return <Line key={index} type="monotone" name={value.replace(':R', ' (prawa oś)')}
                                 dataKey={"dane" + index} stroke={COLORS[serie.length][index]} yAxisId={value.indexOf(':R') > -1?'right':'left'}/>;
                })
                }
        </LineChart>
        </Box>;
    }
}